import _get from 'lodash.get';
import { ListItem, ListItemText, Typography } from '@material-ui/core';
import _isEmpty from 'lodash.isempty';

type TextItemProps<T> = {
	data: T;
	source?: string;
	label: string;
	getValue?: (d: T) => string;
	defaultValue?: string;
	isHiddenEmptyValue?: boolean;
};

function TextItem<T>({
	data,
	source = '',
	label,
	getValue,
	defaultValue,
	isHiddenEmptyValue,
}: TextItemProps<T>) {
	const value = getValue
		? getValue(data)
		: _get(data, source) || defaultValue || '';

	if (isHiddenEmptyValue && _isEmpty(value)) return <></>;

	return (
		<ListItem sx={{ px: 0, py: 1 }}>
			<ListItemText sx={{ minWidth: { xs: '50%', sm: 'auto' }}}>
				<Typography variant="h5">{label}</Typography>
			</ListItemText>
			<Typography
				variant='body2'
				sx={{ textOverflow: 'ellipsis', overflow: 'hidden' }}
			>
				{value || 'N/A'}
			</Typography>
		</ListItem>
	);
}

export default TextItem;
